*{box-sizing:border-box;margin:0;padding:0;font-family:"Trebuchet MS","Lucida Sans Unicode","Lucida Grande","Lucida Sans",Arial,sans-serif}body{background:linear-gradient(90deg, #3010ff 0%, #6473ff 100%)}.main-component{display:flex;flex-direction:row;justify-content:center;height:90vh}.todo-app{display:flex;flex-direction:column;justify-content:start;width:70vmin;height:80vh;background:#161a2b;text-align:center;margin:5vh auto;border-radius:10px;padding:1vh}.img{margin-top:20px;margin-left:20px}h1{margin:30px;color:#e2e2e2;font-size:2vmax}.completerow{opacity:.4}.complete{text-decoration:line-through}.todo-list{overflow-y:auto;height:50vh}.todo-form{margin-bottom:32px}.todo-input{padding:14px 32px 14px 16px;border-radius:4px 0 0 4px;border:2px solid #5d0cff;outline:none;width:30vw;background:transparent;color:#e2e2e2}.todo-input::-webkit-input-placeholder{color:#e2e2e2}.todo-input:-ms-input-placeholder{color:#e2e2e2}.todo-input::placeholder{color:#e2e2e2}.todo-button{padding:16px;border:none;border-radius:0 4px 4px 0;cursor:pointer;outline:none;background:linear-gradient(90deg, #5d0cff 0%, #9b00fa 100%);color:#e2e2e2;text-transform:capitalize}.todo-input.edit{border:2px solid #149fff}.todo-button.edit{background:linear-gradient(90deg, #149fff 0%, #117aff 100%);padding:16px 22px}.todo-container{display:flex;flex-direction:row;position:relative}.todo-name{cursor:pointer;display:flex;margin:auto;color:#fff;padding:15px;width:50vw;padding-left:20px;word-break:break-all}.done{cursor:pointer;color:#fff;z-index:-5;width:10%}.todo-row{display:flex;justify-content:space-between;align-items:center;margin:4px auto;color:#e2e2e2;background:linear-gradient(90deg, #ff7614 0%, #ff5411 100%);border-radius:5px;width:90%}.todo-row:nth-child(4n+1){background:linear-gradient(90deg, #5d0cff 0%, #9b00fa 100%)}.todo-row:nth-child(4n+2){background:linear-gradient(90deg, #ff0cf1 0%, #fa0087 100%)}.todo-row:nth-child(4n+3){background:linear-gradient(90deg, #149fff 0%, #117aff 100%)}.icons{display:flex;align-items:center;font-size:24px;cursor:pointer}.done-icon{color:#fff;margin-left:15px}.delete-icon{margin-right:5px;color:#fff}.edit-icon{margin-right:10px;color:#fff}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background-color:#161a2b}::-webkit-scrollbar-thumb{background-image:linear-gradient(90deg, #5d0cff 0%, #9b00fa 100%);border-radius:20px}.loginbox{width:30vw;height:100vh;display:flex;flex-direction:row;justify-content:center;align-items:center;margin:auto}.loginbt{padding:20px 30px;border:2px solid #f7f7f7;text-align:center;text-transform:uppercase;position:relative;overflow:hidden;transition:.2s;border-radius:10px}.loginbt:hover{cursor:pointer;border-radius:35px}.logoutbox{width:15vw;height:10vh;margin:auto}.logoutbt{padding:20px 30px;border:2px solid #f7f7f7;text-align:center;text-transform:uppercase;font-size:1vmax;position:relative;overflow:hidden;border-radius:35px;transition:.2s}.logoutbt:hover{cursor:pointer;border-radius:10px}@media(max-width: 768px){.logoutbt{padding:10px 15px;font-size:1vmax}}
body{color:#e2e2e2;font-family:"Roboto",sans-serif}.container{margin:0px auto;display:flex;flex-direction:column;align-items:center;justify-content:center}.container h1{text-align:center;font-size:3vmin}.container .progressbar-container{position:relative;width:30vw;height:40px;border:1px solid #e2e2e2;border-radius:10px;display:flex;align-items:center;justify-content:center;overflow:hidden}.container .progressbar-container .progressbar-complete{position:absolute;left:0;top:0px;height:100%;border-radius:10px;-webkit-animation:g 5000ms infinite ease-in-out;animation:g 5000ms infinite ease-in-out;z-index:2}.container .progressbar-container .progressbar-complete .progressbar-liquid{z-index:1;width:70px;height:70px;-webkit-animation:g 5000ms infinite ease-in-out,r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);animation:g 5000ms infinite ease-in-out,r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);position:absolute;right:-5px;top:-10px;border-radius:40%}.container .progressbar-container .progress{z-index:2}@-webkit-keyframes g{0%{background:#5d0cff 0%}50%{background:#9b00fa 100%}100%{background:#5d0cff 0%}}@keyframes g{0%{background:#5d0cff 0%}50%{background:#9b00fa 100%}100%{background:#5d0cff 0%}}@-webkit-keyframes r{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes r{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
